function App() {
  const [selectedTriggerId, setSelectedTriggerId] = useState(0)
  const [selectedRuleId, setSelectedRuleId] = useState(0)
  const [selectedMailCategoryId, setSelectedMailCategory] = useState(0)
  const [selectedTemplateId, setSelectedTemplateId] = useState(0)
  const [selectedSearchFor, setSelectedSearchFor] = useState(1)
  const [searchText, setSearchText] = useState('')
  const [showModal, setShowModal] = useState(false)

  const handleTriggerSelect = ({ target: { value } }) => {
    setSelectedTriggerId(parseInt(value))
    setSelectedRuleId(0)
  }

  const handleSelectRule = ({ target: { value } }) => {
    setSelectedRuleId(parseInt(value))
  }

  const handleSelectTemplate = ({ target: { value } }) => {
    setSelectedTemplateId(parseInt(value))
  }

  const handleSelectSearchFor = ({ target: { value } }) => {
    setSelectedSearchFor(parseInt(value))
  }

  const handleSelectMailCategory = ({ target: { value } }) => {
    setSelectedMailCategory(parseInt(value))
  }

  const selectedTrigger = EMAIL_TRIGGERS.find((t) => t.id === selectedTriggerId) || EMAIL_TRIGGERS[0]
  const selectedRule = EMAIL_RULES.filter((r) => selectedTriggerId === 0 || r.trigger_id === selectedTriggerId)
    .find((r) => r.id === selectedRuleId) || EMAIL_RULES[0]
  const selectedMailCategory = MAIL_CATEGORIES.find((c) => c.id === selectedMailCategoryId) || MAIL_CATEGORIES[0]
  const selectedTemplate = EMAIL_TEMPLATES.find((t) => t.id === selectedTemplateId) || EMAIL_TEMPLATES[0]

  return (
    <div>
      <Container>
        <Row>
          <Col>
            <Form.Label>Email trigger</Form.Label>
            <Form.Select label="Email trigger" value={selectedTriggerId} onChange={handleTriggerSelect}>
              <option>Select trigger</option>
              {EMAIL_TRIGGERS.map((t) => <option value={t.id}>{t.label}</option>)}
            </Form.Select>
          </Col>
          <Col>
            <Form.Label>Email rule</Form.Label>
            <Form.Select label="Email rule" value={selectedRuleId} onChange={handleSelectRule}>
              <option>Select rule</option>
              {EMAIL_RULES
                .filter((r) => selectedTriggerId === 0 || r.trigger_id === selectedTriggerId)
                .map((r) => <option value={r.id}>{r.label}</option>)}
            </Form.Select>
          </Col>
          <Col>
            <Form.Label>Email template</Form.Label>
            <Form.Select label="Mail template" value={selectedTemplateId} onChange={handleSelectTemplate}>
              <option>Select email template</option>
              {EMAIL_TEMPLATES.map((c) => <option value={c.id}>{c.name}</option>)}
            </Form.Select>
          </Col>
          <Col>
            <Form.Label>Mail category</Form.Label>
            <Form.Select label="Mail category" value={selectedMailCategoryId} onChange={handleSelectMailCategory}>
              <option>Select mail category</option>
              {MAIL_CATEGORIES.map((c) => <option value={c.id}>{c.label}</option>)}
            </Form.Select>
          </Col>
        </Row>
        <br />
        <Row>
          <Col>
            <Form.Group className="mb-3" controlId="search">
              <Form.Label>Search emails with</Form.Label>
              <Form.Select label="Mail category" value={selectedSearchFor} onChange={handleSelectSearchFor}>
                {searchFor.map((c) => <option value={c.id}>{c.label}</option>)}
            </Form.Select>
            </Form.Group>
          </Col>
          <Col>
            <Form.Group className="mb-3" controlId="search">
              <Form.Label>Text</Form.Label>
              <Form.Control type="text" placeholder="Search text" onChange={setSearchText} value={searchText} />
            </Form.Group>
          </Col>
          <Col></Col>
          <Col></Col>
        </Row>
      </Container>
      <br />
      <br />
      <Container>
        <Row>
          <Col>
            <Table striped bordered hover textXSmall>
              <thead>
                <tr>
                  <th>Trigger</th>
                  <th>Rule</th>
                  <th>Template</th>
                  <th>Category</th>
                  <th>Receiver type</th>
                  <th>Receiver</th>
                  <th>email</th>
                  <th>Time</th>
                  <th></th>
                </tr>
              </thead>
              <tbody>
                {receivers.map(({ name, email }) => (
                  <tr>
                    <td><Badge bg="secondary">{selectedTrigger.label}</Badge></td>
                    <td><Badge bg="secondary">{selectedRule.label}</Badge></td>
                    <td><Badge bg="secondary">{selectedTemplate.name}</Badge></td>
                    <td><Badge bg="secondary">{selectedMailCategory.label}</Badge></td>
                    <td><Badge bg="secondary">responsible</Badge></td>
                    <td><Badge bg="secondary">{name}</Badge></td>
                    <td><Badge bg="secondary">{email}</Badge></td>
                    <td>2021-07-02 00:00:00</td>
                    <td><Button variant="success" size="sm" className="float-right align-middle" onClick={() => setShowModal(true)}><BoxArrowInRight /></Button></td>
                  </tr>
                ))}
              </tbody>
            </Table>
          </Col>
        </Row>
      </Container>

      <Modal show={showModal} onHide={() => setShowModal(false)} dialogClassName="modal-90w">
        <Modal.Header closeButton>
          <Modal.Title>Mail details</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Table striped bordered hover textXSmall>
            <tbody>
              <tr><td>Trigger:</td><td><Badge bg="secondary">{selectedTrigger.label}</Badge></td></tr>
              <tr><td>Rule:</td><td><Badge bg="secondary">{selectedRule.label}</Badge></td></tr>
              <tr><td>Template:</td><td><Badge bg="secondary">{selectedTemplate.name}</Badge></td></tr>
              <tr><td>Category:</td><td><Badge bg="secondary">{selectedMailCategory.label}</Badge></td></tr>
              <tr><td>Receiver type:</td><td><Badge bg="secondary">responsible</Badge></td></tr>
              <tr><td>Subject:</td><td><Badge bg="secondary">Aktivierung Ihres IMS-Accounts</Badge></td></tr>
              <tr><td>Receiver:</td><td><Badge bg="secondary">{receivers[0].name}</Badge></td></tr>
              <tr><td>E-mail:</td><td><Badge bg="secondary">{receivers[0].email}</Badge></td></tr>
              <tr><td>Time:</td><td>2021-07-02 00:00:00</td></tr>
            </tbody>
          </Table>
          <h3>Log</h3>
          <JSONPretty data={logRecord} theme={JSONPrettyMon}></JSONPretty>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={() => setShowModal(false)}>
            Close
          </Button>
        </Modal.Footer>
      </Modal>
    </div>
  );
}

